import React, { useEffect, useState } from 'react'
import EmployeeTable from './EmployeeTable'
import EmpInvestmentFilters from './EmpInvestmentFilters'
import { pagination } from '../../../utils/Constants.config'
import { useDispatch, useSelector } from 'react-redux'
import { departmentAction, employeeActions, payrollActions } from '../../../core/resource/actions'
import { DynamicFilter } from '../../commanComponent'
import { multiSelectIDs } from '../../../utils/common'

const PayrollEmployees = () => {
    const dispatch = useDispatch()

    const {
        getAllEmployeeAssignStatusPending,
        getAllEmployeeAssignStatusRes,
        getAllEmployeeAssignStatusError,
    } = useSelector(state => state.payroll)
    const {
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    } = useSelector(state => state.department)

    const {
        getAllEmployeePending,
        getAllEmployeeRes,
        getAllEmployeeError,
    } = useSelector(state => state.employee)

    const [currentPage, setCurrentPage] = useState(1);
    const [allEmployeesData, setAllEmployeesData] = useState('')
    const [allEmployeeList, setAllEmployeeList] = useState('')
    const [allDepartments, setAllDepartments] = useState('')
    const [filterFormData, setFilterFormData] = useState('')
    const [filterData, setFilterData] = useState('')

    useEffect(() => {
        getAllEmployeesList(filterData)
    }, [currentPage])

    const getAllEmployeesList = (payload) => {
        const params = {
            page: currentPage,
            limit: pagination.limit,
            ...payload
        }
        console.log(params, "params")
        dispatch(payrollActions.getAllEmployeeAssignStatusRequest(params));
    }

    useEffect(() => {
        if (!getAllEmployeeAssignStatusPending) {
            if (getAllEmployeeAssignStatusError) {
                return;
            }
            if (getAllEmployeeAssignStatusRes) {
                if (getAllEmployeeAssignStatusRes?.code == 200) {
                    const employeeListResponse = getAllEmployeeAssignStatusRes?.data
                    setAllEmployeesData(employeeListResponse)
                }
            }
        }
    }, [
        getAllEmployeeAssignStatusRes,
        getAllEmployeeAssignStatusError,
        getAllEmployeeAssignStatusPending
    ]);

    useEffect(() => {
        getAllDepartments()
        getAllEmployee()
    }, [])
    /* Get All Employee List*/
    const getAllEmployee = () => {
        dispatch(employeeActions.getAllEmployeeRequest())
    }
    useEffect(() => {
        if (!getAllEmployeePending) {
            if (getAllEmployeeError) {
                return;
            }
            if (getAllEmployeeRes) {
                if (getAllEmployeeRes?.code === 200) {

                    setAllEmployeeList(getAllEmployeeRes?.data)
                }
            }
        }
    }, [
        getAllEmployeeRes,
        getAllEmployeeError,
        getAllEmployeePending
    ]);



    /* Get All Departments */
    const getAllDepartments = () => {
        if (!getDepartmentListRes?.data?.length) {
            dispatch(departmentAction.getDepartmentListRequest());
        } else {
            setAllDepartments(getDepartmentListRes?.data)
        }
    }

    useEffect(() => {
        if (!getDepartmentListPending) {
            if (getDepartmentListError) {
                return;
            }
            if (getDepartmentListRes) {
                if (getDepartmentListRes?.code === 200) {
                    const departmentResponse = getDepartmentListRes?.data
                    setAllDepartments(departmentResponse)
                }
            }
        }
    }, [
        getDepartmentListRes,
        getDepartmentListError,
        getDepartmentListPending
    ]);

    const employeeFilters = [
        {
            name: 'selectedEmployees',
            label: 'Select Employee / ID',
            type: 'select',
            value: filterFormData.selectedEmployees,
            data: allEmployeeList,
            placeholder: 'Enter Employee Name',
            isMulti: true
        },
        {
            name: 'selectedDepartment',
            label: 'Department',
            type: 'select',
            data: allDepartments,
            value: filterFormData.selectedDepartment,
            placeholder: 'Select Department',
            isMulti: true
        },

    ];


    const handleFilterFormData = (e, fieldName) => {
        const name = fieldName || e.target?.name;
        const { value, type, checked } = e?.target ? e.target : { value: e };

        setFilterFormData((prevState) => ({
            ...prevState,
            [name]: type === 'checkbox' ? checked : value,
        }));
    };

    const handleFilterEmployees = () => {
        const filterFields = {};
        if (filterFormData?.selectedDepartment) filterFields["department_ids"] = multiSelectIDs(filterFormData?.selectedDepartment)
        if (filterFormData?.selectedEmployees) filterFields["employee_ids"] = multiSelectIDs(filterFormData?.selectedEmployees)
        getAllEmployeesList(filterFields)
        setFilterData(filterFields)
    }


    return (
        <>
            <DynamicFilter
                filters={employeeFilters}
                handleFilterFormData={handleFilterFormData}
                onSubmitHandleFilter={handleFilterEmployees}
            />
            <div className='cardBox mt-3'>
                <div className=''>
                    <div className="d-flex justify-content-between align-items-center mb-2">
                        <h3 className='underlineHead ' style={{ fontSize: "18px" }}> Employees</h3>
                    </div>
                    <EmployeeTable
                        allEmployees={allEmployeesData}
                        setCurrentPage={setCurrentPage}
                        currentPage={currentPage}
                        isLoading={getAllEmployeeAssignStatusPending}
                    />
                </div>
            </div>
        </>
    )
}

export default PayrollEmployees

